import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import {SocialData, HomeIcon} from '../context/SocialData';
import Menu from './Menu';
//yarn add styled-components

const NavBar = () => {
  const [size, setSize] = useState ([window.innerWidth, window.innerHeight]);
  const [width, height] = [...size];

  useEffect (() => {
    const handleResize = () => {
      setSize ([window.innerWidth, window.innerHeight]);
    };
    window.addEventListener ('resize', handleResize);
    /////BOA PRATICA, LIMPAR EVENTO COM RETURN//
    return () => {
      window.removeEventListener ('resize', handleResize);
    };
  });
  if (width >= 640 && height > 450) {
    return (
      <NavWrapper>
        <div className="nav-rotate">
          <div className="icons">
            {SocialData.map (item => (
              <a
                className="iconJS"
                href={item.url}
                target="blank"
                key={item.id}
              >
                {item.icon}
              </a>
            ))}
          </div>
          <div>{/* LAYOUT ESPACO */}</div>
          <div>Contato</div>
          <div>Sobre Mim</div>
          <div>Projetos</div>
          <div>{/* LAYOUT ESPACO */}</div>
          <div className="iconHome">
            <a className="iconJS" href={HomeIcon.url}>
              {HomeIcon.icon}
            </a>
          </div>
        </div>
      </NavWrapper>
    );
  } else {
    return <Menu />;
  }
};

const NavWrapper = styled.nav`
  .nav-rotate {
    text-transform: uppercase;
    color: whitesmoke;
    width: 100vh;
    height: 16vh;
    font-size: 20px;
    font-family: Oxygen;
    background: #282c34;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-around;
    -webkit-transform-origin: left top;
    -webkit-transform: rotate(-90deg) translateX(-100%);
  }
  .icons {
    width: 22vh;
    font-size: 16=8px;
    display: flex;
    justify-content: space-evenly;
  }
  .iconJS {
    color: whitesmoke;
    -webkit-transform-origin: left top;
    -webkit-transform: rotate(-270deg) translateX(25%);
    transition: 1s;
    &:hover {
      color: #61dafb;
    }
  }
  .iconHome {
    font-size: 26px;
    display: flex;
  }
`;

export default NavBar;
