import React from 'react';
import styled from 'styled-components';
import mainBcg from '../../images/pc5.jpg';
import IconDown from './IconDown';
import Typical from 'react-typical';

export default function Projects({img = mainBcg}) {
  return (
    <ProjectsWrapper img={img}>
      <main id="home" className="container-fluid">
        <h1 className="lg-heading">
          Everton <span className="text-secondary">SÃ¡vio</span>
        </h1>
        <h1 className="sm-heading">
          <Typical
            steps={[
              '#',
              1500,
              '#Full',
              750,
              '#Full-',
              50,
              '#Full-Stack',
              1500,
              '#Full',
              500,
              '#',
              1000,
              '#Data ',
              750,
              '#Data Scientist',
              1000,
            ]}
            loop={1}
            wrapper="p"
          />
          {/* Data Scientist Full-Stack */}
        </h1>
      </main>
      <div className="icone"><IconDown /></div>
    </ProjectsWrapper>
  );
}

const ProjectsWrapper = styled.div`

  background: linear-gradient(var(--primaryRGBA), var(--primaryRGBA)),
  url(${props => props.img}) center/ cover no-repeat;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;  
  border-radius: 5px;
  border-radius: 5px;

  .icone{
    position: absolute;
    left: 49%
  }
h1, h2, h3 {
  margin: 0;
  font-weight: 400;
  color: #fff;
  &.lg-heading {
    font-size: 6rem;
    padding-bottom: 1rem;
  }

  &.sm-heading {
    padding: 1rem 0rem 0.5rem 0.5rem;
    font-size: 2.5rem;
    border-radius: 5px;
    background-color: #282c34;
    letter-spacing: 0.2ch;
  }
}

.text-secondary{
color: #eece1a !important;
}

main {
  min-height: 100vh;
  padding-top: 15vh;
}

`;
