import React from 'react';
import styled from 'styled-components';
import Card from '../Card';

const SectionProjects = () => {
  return (
    <SectionProjectsWrapper>
      <section className="main" id="portfolio">
        <h2>Section 2</h2>
        <div className="row">
          <div className="col">
            <Card />
          </div>
          <div className="col">
            <Card />
          </div>
          <div className="col">
            <Card />
          </div>
          <div className="col">
            <Card />
          </div>
          <div className="col">
            <Card />
          </div>
          <div className="col">
            <Card />
          </div>
        </div>

        <a href="#section1">Click Me to Smooth Scroll to Section 1 Above</a>
      </section>
    </SectionProjectsWrapper>
  );
};

const SectionProjectsWrapper = styled.div`
  .main {
    position: relative;
    margin-top: 1vh;
    border-radius: 5px;
  }
  #portfolio {
    min-height: 100vh;
    background-color: whitesmoke;
  }
`;

export default SectionProjects;
